import Vue from 'vue';
import App from '../App';
import IHNav from '../components/Nav';
import router from '../router';
import store from '../components/User';
console.log(store);

Vue.config.productionTip = false;


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App, IHNav},
});



